	import pickRegions from '@/components/pick-regions/pick-regions.vue'
	import {doCreateUser,getRegCode} from '@/api/supervisor/manager.js'
	
	var tha,js;
	export default {
		components:{
		    pickRegions
		},
		onLoad(){
			tha = this;
			
		},
		onUnload(){
			clearInterval(js)
			this.second = 0;
		},
		data() {
			return {
				userinfo:{
					loginName:'',
					passWord:'',
					passWord2:'',
					code:'',
					address:'',
					oldCard:'',
					phone:''
				},
				detailAdress:'',
				code:'',
				xieyi:true,
				showPassword:false,
				showPassword2:false,
				second:0,
				regions:[],
				defaultRegions:['浙江省','杭州市','桐庐县'],
				regpwd:false,
				regphone:false,
				havecode:false
			};
		},
		computed:{
			yanzhengma(){
				if(this.second==0){
					return '获取验证码';
				}else{
					if(this.second<10){
						return '重新获取0'+this.second;
					}else{
						return '重新获取'+this.second;
					}
				}
			},
			regionsName(){
			    // 转为字符串
			    return this.regions.map(item=>item.name).join(' ')
			}
		},
		methods: {
			prop_pwd(value){
				if(value!==''&&value!=null)
				{
				  var pwdRegex = new RegExp('(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]).{8,30}');
				  if (!pwdRegex.test(value))
				  {
					uni.showToast({
					    icon: 'none',
					    title: '您的密码复杂度太低（密码中必须包含字母、数字、特殊字符），请重新输入密码！',
						duration:2000
					});
					this.regpwd=false
					return;
				  }
				  this.regpwd=true;
				}else{
					this.regpwd=false
				}
				
			},
			prop_oldcard(value){
				if(value=='' || value==null){
					uni.showToast({
					    icon: 'none',
					    title: '请填写老年卡号',
						duration:2000
					});
					return;
				}
			},
			prop_pwd2(value){
				if(value!==''&&value!=null)
				{
				  var pwdRegex = new RegExp('(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]).{8,30}');
				  if (!pwdRegex.test(value))
				  {
					uni.showToast({
					    icon: 'none',
					    title: '您的密码复杂度太低（密码中必须包含字母、数字、特殊字符），请重新输入密码！',
						duration:2000
					});
					this.regpwd=false
					return;
				  }
				  if(value!=this.userinfo.passWord){
					  uni.showToast({
					      icon: 'none',
					      title: '两次密码不一致',
						  duration:2000
					  });
					  this.regpwd=false
					  return;
				  }
				  this.regpwd=true;
				}else{
					this.regpwd=false
				}
			},
			prop_phone(value){
				if (value !== '' && value !== null) {
				  var reg = /^1[3456789]\d{9}$/;
				  if (!reg.test(value)) {
				    uni.showToast({
				        icon: 'none',
				        title: '手机号码不合法',
						duration:2000
				    });
				    this.regphone=false
				    return;
				  }else{
					  this.regphone=true
				  }
				} else {
					uni.showToast({
					    icon: 'none',
					    title: '手机号码不能为空',
						duration:2000
					});
					this.regphone=false
				}
				
			},
			// 获取选择的地区
			handleGetRegions(regions){
				var address='';
				for(var i=0;i<regions.length;i++)
				{
					address+=regions[i].name;
				}
			    this.userinfo.address = address
			},
			display() {
			    this.showPassword = !this.showPassword
			},
			display2() {
			    this.showPassword2 = !this.showPassword2
			},
			// xieyitong(){
			// 	this.xieyi = !this.xieyi;
			// },
			getcode(){
				var that=this;
				if(!this.regphone){
					uni.showToast({
					    icon: 'none',
					    title: '请输入正确的手机号'
					});
					return;
				}

				if(this.havecode)
				{
					uni.showToast({title:"请等待！"});
					return;
				}
				this.havecode=true;
				// this.havecode=false;
				//到计时
				
				this.second = 60;
				js = setInterval(function(){
					tha.second--;
					if(tha.second<=0){
						that.havecode=false;
						clearInterval(js)
					}
				},1000)
				
				getRegCode(this.userinfo.phone).then(res=>{
					if(res.data.code==200)
					{
						this.code=res.data.data.str;
						uni.showToast({title:res.data.message});
						
					}else{
						uni.showToast({title:res.data.message,icon:'none'});
					}
				}).catch(error=>{
                console.log(error)
              });
			
			},
		    bindLogin() {
				
				if (this.userinfo.loginName=='' || this.userinfo.loginName==null) {
				    uni.showToast({
				        icon: 'none',
				        title: '请输入账号'
				    });
				    return;
				}
				if (!this.regpwd) {
				    uni.showToast({
				        icon: 'none',
				        title: '请检查密码'
				    });
				    return;
				}
				if (!this.regphone) {
				    uni.showToast({
				        icon: 'none',
				        title: '请检查手机号'
				    });
				    return;
				}
				if (this.detailAdress =='' ) {
				    uni.showToast({
				        icon: 'none',
				        title: '请输入详细地址'
				    });
				    return;
				}
				if (this.code=='' ||  this.code.toLowerCase() != this.userinfo.code.toLowerCase()) {
				    uni.showToast({
				        icon: 'none',
				        title: '验证码不正确'
				    });
				    return;
				}
				
				this.userinfo.address+="-"+this.detailAdress;
				console.log(this.userinfo);
				let params={RealName:this.userinfo.loginName,PassWord:this.userinfo.passWord,
				Address:this.userinfo.address,OldCard:this.userinfo.oldCard,Phone:this.userinfo.phone};
				doCreateUser(params).then(res=>{
					console.log(res.data)
					if(res.data.code==200)
					{
						uni.showToast({title:res.data.message});
						setTimeout(function(){
							uni.navigateBack();
						},2000) 
					}else{
						uni.showToast({title:res.data.message,icon:'none'});
					}
					console.log();
				});

				
		    }
			
		}
	}
